.mailing {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  width: 100%;
}

form {
  align-items: center;
  justify-content: center;
  width: 500px;
  padding: 1rem;
}

input,
textarea {
  width: 100%;
  padding: 0.5rem;
  margin: 0.2rem;
  box-sizing: border-box;
  font-family: 'NIXGONM-Vb';
  border: none;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.1);
}

input:focus,
textarea:focus {
  border-color: #000;
}

textarea {
  vertical-align: top;
  height: 15rem;
  resize: none;
}

.button {
  display: flex;
  justify-content: center;
  margin-top: 2rem;
}

#btn {
  background-color: black;
  color: white;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 5vh;
  font-family: 'NIXGONM-Vb';
}

#success {
  // display: none;
  text-align: center;
}

@media only screen and (max-width: 1024px) {
}

@media only screen and (max-width: 768px) {
}

@media only screen and (max-width: 480px) {
  form {
    width: 300px;
  }
}

@media only screen and (max-width: 320px) {
  form {
    width: 250px;
  }
}
