.rows {
  width: 100%;
  display: flex;
}

.contact {
  padding: 5vh 0 10vh;
  animation-duration: 1s;
  animation-name: slidein;
  font: {
    family: 'Montserrat', sans-serif;
    size: 4rem;
  }
}

.selection {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.ct-btn {
  border: none;
  background: none;
  margin: 0 2rem;
  width: 4.5rem;
  height: auto;
  outline: none;
  &:hover {
    filter: drop-shadow(0px 0px 20px black);
  }
}

@media only screen and (max-width: 1024px) {
  .contact {
    font-size: 3.5rem;
  }
  .ct-btn {
    margin: 0 2rem;
    width: 4rem;
  }
}

@media only screen and (max-width: 768px) {
  .contact {
    font-size: 3rem;
  }
  .ct-btn {
    margin: 0 0.5rem;
    width: 4rem;
  }
}

@media only screen and (max-width: 480px) {
  .contact {
    font-size: 2.5rem;
  }
}

@media only screen and (max-width: 320px) {
  .contact {
    font-size: 2rem;
  }
  .ct-btn {
    width: 3.5rem;
  }
}
