.here {
  display: flex;
  justify-content: center;
  padding: 0.7rem;
  font: {
    family: 'NIXGONM-Vb';
  }
}

.home {
  overflow-x: hidden;
}

.hello {
  overflow: hidden;
  display: flex;
  padding: 20vh 0 20vh;
  justify-content: center;
  font: {
    family: 'Montserrat', sans-serif;
    size: 60px;
  }
  animation-duration: 0.5s;
  animation-name: slidein;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 300%;
  }
  to {
    margin-left: 0%;
    width: 100%;
  }
}

.desc {
  overflow: hidden;
  display: flex;
  justify-content: center;
  padding: 0 0 10px 0;
  font: {
    family: 'Montserrat', sans-serif;
    size: 20px;
  }
  animation-duration: 1s;
  animation-name: slidein;
}

.details {
  overflow: hidden;
  display: flex;
  justify-content: center;
  padding: 20px 0 80px 0;
  animation-duration: 1.5s;
  animation-name: slidein;
}

.detailBtn {
  font: {
    family: 'Montserrat', sans-serif;
    size: 15px;
  }
  cursor: pointer;
  color: white;
  overflow: hidden;
  padding: 15px;
  background-color: rgba(30, 22, 54, 0.6);
  box-shadow: rgba(30, 22, 54, 0.4) 0 0px 0px 2px inset;
  border: none;
  border-radius: 10px;
  &:hover {
    background-color: rgba(253, 253, 253, 0.6);
    color: black;
  }
  &:active {
    background-color: rgba(30, 22, 54, 0.6);
    box-shadow: 0 0px 0px 2px rgba(30, 22, 54, 0.4);
    transform: translateY(2px);
    color: white;
  }
}

@media only screen and (max-width: 1024px) {
  .about {
    padding: 20vh 0 20vh;
    font: {
      size: 10px;
    }
  }
  .desc {
    font: {
      size: 15px;
    }
  }
}

@media only screen and (max-width: 768px) {
  .hello {
    padding: 15vh 0 15vh;
    font: {
      size: 40px;
    }
  }
  .desc {
    font: {
      size: 13px;
    }
  }
}

@media only screen and (max-width: 480px) {
  .hello {
    padding: 10vh 0 10vh;
    font: {
      size: 30px;
    }
  }
  .desc {
    font: {
      size: 12px;
    }
  }
}

@media only screen and (max-width: 320px) {
  .hello {
    padding: 10vh 0 10vh;
    font: {
      size: 20px;
    }
  }
  .desc {
    font: {
      size: 10px;
    }
  }
}
